import React from 'react';
import { Smartphone } from 'lucide-react';
import { getLogoUrl } from '../utils/brandingService';

type Props = {
  size?: number;
  className?: string;
  rounded?: boolean;
};

export default function BrandLogo({ size = 32, className = '', rounded = true }: Props) {
  const [url, setUrl] = React.useState<string | undefined>();
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    let mounted = true;
    (async () => {
      try {
        const u = await getLogoUrl();
        if (mounted) setUrl(u);
      } finally {
        if (mounted) setLoading(false);
      }
    })();
    return () => {
      mounted = false;
    };
  }, []);

  const style = {
    width: size,
    height: size,
  } as React.CSSProperties;

  if (!url) {
    return (
      <div
        className={`${rounded ? 'rounded-md' : ''} bg-blue-600 flex items-center justify-center ${className}`}
        style={style}
      >
        <Smartphone className="text-white" size={Math.max(16, size - 12)} />
      </div>
    );
  }

  return (
    <img
      src={url}
      alt="Brand Logo"
      className={`${rounded ? 'rounded-md' : ''} ${className}`}
      style={style}
    />
  );
}